@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@font-face {
  font-family: "BalboaPlus";
  src: local("BalboaPlus"),
    url(/ga08-mernolith-front/static/media/balboaplus-inline.a74eebdd.ttf) format("truetype");
}

* {
  margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  width: 100vw;
  height: auto;
  min-height: 100vh;
  display: block;
  position: absolute;
  top: 0px;
  left: 0px;
  background: #424242;
  background-image: url(https://i.imgur.com/yj4M284.png);
  background-size: cover;
  background-attachment: "fixed";
}

h1 {
  font-family: "BalboaPlus", sans-serif;
  font-size: 12vh;
  padding-top: 4%;
}

.navbar {
  background-color: black;
  color: white;
  width: 100%;
  height: 16vh;
  min-height: 69px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 2;
  justify-content: space-between;
}

.title {
  letter-spacing: 4px;
  left: 50vw;
  transform: translate(-50%);
  position: relative;
}

.button > button {
  color: white;
}

.buttons {
  display: flex;
  padding-right: 2vw;
  justify-content: center;
  align-items: center;
}

.IconButton {
  color: white;
  font-size: 20px;
  padding: 20px;
}

.directors_container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 10px 10px;
  padding: 2vw;
}

.universal {
  background: #424242;
  background-image: url(https://i.imgur.com/yj4M284.png);
  background-size: cover;
}

a {
  color: white;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  text-decoration: none;
}

.director {
  width: 100%;
  height: 69vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: center;
}

.director:hover {
  box-shadow: 0 0 10px black;
}

.hover__name {
  display: none;
  width: 100%;
  padding-top: 10px;
  background: black;
}

.director:hover > .hover__name {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.film__background {
  background: rgba(0, 0, 0, 0);
}

.allFilms__container {
  display: grid;
  grid-template-columns: 23.5vw 23.5vw 23.5vw 23.5vw;
  margin: 2vw;
  grid-gap: 1vw;
}

.film {
  height: 33vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.film:hover {
  box-shadow: 0 0 10px black;
}

.directorFilm__container {
  display: grid;
  grid-template-columns: 23.5vw 23.5vw 23.5vw 23.5vw;
  margin: 2vw;
  grid-gap: 0.333vw;
}
.SelectedDirectorFilm {
  display: flex;
  justify-content: center;
  align-items: center;
}
.director__container {
  background: white;
  padding: 2vw;
}

.directorFilm {
  height: 33vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

h2 {
  font-family: "BalboaPlus", sans-serif;
  font-size: 60px;
}

.modal__background {
  height: 40vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}

.modal__container {
  height: 40vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}

/* NOTE!!!!  DO NOT MAKE CHANGES TO MODAL_IMAGE WIDTH PROPERTY WITHOUT CHECKING CONTENT_BACKGROUND BELOW. */
.modal__image {
  height: 40vw;
  width: 25vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.flex__flex {
  height: 40vw;
  width: 75vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}

/* NOTE!!!! THE MIN-WIDTH PROPERTY DEPENDS ON THE WIDTH PROPERTY OF MODAL_IMAGE, ABOVE */
.content__background {
  /* min-width: 70vw; */
  background-color: rgba(14, 13, 13, 0.777);
  padding: 25px;
}

.words__flex {
  height: auto;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.modal__words {
  display: flex;
  flex-direction: column;
  color: white;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  padding-top: 12px;
  padding-right: 1.5vw;
}

.buttons__flex {
  width: 75vw;
  height: auto;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.modal__buttons {
  background-color: rgba(0, 0, 0, 0);
  padding-top: 25px;
}

.form__container {
  width: 95vw;
  background-color: rgba(14, 13, 13, 0.897);
  color: white;
  margin: 20px;
  display: flex;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.form__container > textarea {
  width: 80%;
  margin: 20px;
  resize: none;
  overflow: scroll;
}

